.sections-regulamin {
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    align-items: center;
    margin-bottom: 100px;
  }
  
  .regulamin-text {
    text-align: justify;
    color: #808080;
    font-size: 18px;
    font-weight: 500;

    p{
       margin-top: 20px;
       line-height: 1.5;
    }
    strong{
        color: #666666;
    }
    a{
        text-decoration: none;
        color: $blue;
        &:hover{
            color: #666666;
        }
    }
  }