.header-select {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  
  &:hover {
    .header-menu-link{
      color: $blue;
      text-decoration: none;
      svg{
          transform: rotate(90deg);
          fill: $blue;
      }
    }
    .header-select__title {
      color: $blue;
      text-decoration: none;
    }
    .drop-svg {
      margin-top: -2.5px;
      transform: rotate(180deg);
      fill: $blue;
    }
    .header-select__dropdown {
      display: block;
  }
}
}


.header-select-dropdown__icon {
  .drop-svg {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 30px;
    content: "";
    display: block;
    width: 8px;
    height: 5px;
    fill: #b3b3b3;
    font-size: 0;
  }
}
.header-select__title {
  position: relative;
  width: 83px;
  color: #b3b3b3;
  font-size: 10px;
  font-weight: 900;
  line-height: 1;
  text-transform: uppercase;
  letter-spacing: 1.25px;
}
.header-select__dropdown {
  position: absolute;   
  top: 100%;
  left: -2px;
  right: -2px;
  border: 2px solid #f5f5f5;
  border-radius: 0 0 4px 4px;
  background-color: white;
  z-index:1000;

}
.header-select__dropdown-item {
  height: 40px;
  font-size: 13px;
  font-weight: 500;
  color: rgb(128, 128, 128);
  justify-content: center;
  display: flex;
  align-items: center;
  border-bottom: 2px solid #f5f5f5;
  &:last-child {
    border-bottom: none;
  }
  &:hover {
    background-color: #ebebeb;
    color:rgb(128, 128, 128);
    
  }
  
}


