.kart-section__row {
    display: flex;
    justify-content: center;
   
}
.kart-section__col {
    
    width: 525px;
    & + & {
        margin-left: 25px;
    }
    
}
.kart-section-item-link-wrapper {
    display: block;

}

.kart-section-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 30px;
    max-width: 525px;
    height: 650px;
    text-align: center;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.24);
    border-radius: 6px;
    background-color: #ffffff;
    transition: box-shadow 0.2s ease-in,  transform 0.2s ease-in;

  

    &:hover {
        transform: translateY(-4px);
        box-shadow: 0 3px 25px rgba(0, 0, 0, 0.619);
    }
}

.kart-section-item--background-img {
    justify-content: flex-end;
    background-color: #3b4b48;
    background-position: center;
    background-size: cover;
}
.kart-section {
  display: flex;
  margin-top: 10px;
 
}

.kart-position{
    width: 100%;
    & + & {
        margin-left: 10px;
}
}

.zaciemnienie{
    transition: opacity 0.2s ease-in;
    opacity: 0.6;
    cursor: pointer;
    &:hover{
        opacity: 1;
    }
    
}

.sections-kart {
    padding-left: 15px;
    padding-right: 15px;
    display: flex;
    align-items: center;
  }
  
  .kart-text {
    text-align: justify;
    color: #808080;
    font-size: 20px;
    font-weight: 600;
    line-height: 1;

    p{
       margin-top: 20px;
    }
    h1{
        margin-top: 10px;
        color: #3b4b48;
        font-size: 28px;
        font-weight: 800;
    }
    h2{
        margin-top: 40px;
        color: #3b4b48;
        font-size: 24px;
        font-weight: 900;
    }
    h3{
        margin-top: 40px;
        color: #3b4b48;
        font-size: 20px;
        font-weight: 900;
    }
    li{
        color: #666666;
        font-size: 20px;
        font-weight: 600;
    }

  }
