.page__position{
    @extend %mixin-card;
    //padding: 0 100px 0 100px;
    display: flex;
    width: 100%;
    height: 100px;
    margin-bottom: 30px;
    justify-content: center;
    align-items: center;
    
}
.page__container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.page__back {
    display: flex;

}
.link__back{
    outline: none;
    padding: 0;
    cursor: pointer;
    text-align: center;
   

    transition:  transform 0.3s ease-in;

    &:hover {
        transform: translateX(-7px);
        .title__back {
            color: $blue;
        }
        .svg___back{
            fill: $blue;
    }
    }
}
.svg___back{
    transform: rotate(90deg);
    width: 50px;
    fill: #666666;
}

.title__back {

    color: #666666;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.5;
}

.page-container {
    display: flex;
    margin-left: 40px;
    margin-right: 40px;

    
}
.page {
    transition:  border 0.1s ease-in;
    width: 65px;
    height: 65px;
    text-align: center;

    border: 7px solid #666666;
    border-radius: 50%;
    background: transparent;

    top: 7%;
    transform: translateY(-7%);

    color: #666666;
    opacity: 0.8;
    font-size: 22px;
    font-weight: 700;
    line-height: 2.2;
    & + & {
        margin-left: 20px;
    }
    a{
        color: inherit;
        text-decoration: none;
        display: block;
    }
    &:hover{
        cursor: pointer;
        color: $blue;
        text-decoration: none;
        border: 9px solid $blue;
        opacity: 1;
    }

}
.page-active {
    color: $blue;
    border: 7px solid $blue;
    opacity: 1;
}


.page__next {
    display: flex;
    
}
.link__next{
    outline: none;
    padding: 0;
    cursor: pointer;
    text-align: center;
   
    transition:  transform 0.3s ease-in;

    &:hover {
        transform: translateX(7px);
        .title__next {
            color: $blue;
        }
        .svg___next{
            fill: $blue;
    }
    }
}
.svg___next{
    transform: rotate(-90deg);
    width: 50px;
    fill: #666666;
}

.title__next {
    color: #666666;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.5;
}