.social {
    display: flex;
    align-items: center;

}
.social__item {
    width: 18px;
    text-decoration: none;

    display: flex;
    align-items: center;
    justify-content: center;

    &+&{
        margin-left: 34px;
    }
    .footer_icons{
        fill: #666666;
    }
    &:hover {
        .footer_icons-facebook {
            fill: #3b5998;
          }
          .footer_icons-instagram {
            fill: #772362;
          }
          .footer_icons-youtube {
            fill:  #c4302b ;
          }
    }
}
