.sections-nowosci {
  padding-left: 15px;
  padding-right: 15px;
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}

.nowosci-text {
  color: #808080;
  font-size: 20px;
  font-weight: 700;
  line-height: 1;
  margin-right: 10px;
}

.sections-nowosci_center {
    display: flex;
    align-items: center;
    margin-top: 60px;
    margin-bottom: 30px;
   
    @extend %mixin-card;
    padding: 0 40px;
    width: 100%;
    height: 60px;
    justify-content: center;
  }

.nowosci-text_center{
    color: #666666;
    font-size: 20px;
    font-weight: 700;
    line-height: 1;
    
}
.nowosci__item {
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;

  .footer_icons {
    fill: #666666;
  }
  &:hover {
    .footer_icons-facebook {
      fill: #3b5998;
    }
    .footer_icons-instagram {
      fill: #772362;
    }
    .footer_icons-youtube {
      fill:  #c4302b ;
    }
    .nowosci-text-link {
      color: $blue;
    }
  }
}
.nowosci-text-link {
  color: #666666;
  font-size: 20px;
  font-weight: 800;
  line-height: 1;
  margin-right: 5px;
  text-decoration: none;
}
