.header-menu-link{
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    width: 100%;
    height: 100%;
    color: #808080;
    
    
    &:hover{
        color: $blue;
        text-decoration: none;
        svg{
            transform: rotate(90deg);
            fill: $blue;
        }
        .header-select__dropdown {
            display: block;
            
            
          }
    }

}
.header-menu-link__icon{
    margin-right: 22px;
    svg{
        fill: gray;

    }

}
.header-menu-link__text{
    font-size: 16px;
    font-weight: 600;
    line-height: 1;

}
