.kosz__position{
    @extend %mixin-card;
    display: flex;
    width: 100%;
    height: 65px;
    margin-top: 50px;
    margin-bottom: 30px;
    justify-content: center;
    align-items: center;
    
}
.kosz__container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.kosz__minus {
    display: flex;

}
.btn_minus{
    border: none;
    background: transparent;
    outline: none;
    padding: 0;
    cursor: pointer;
    text-align: center;
   

    transition:  transform 0.3s ease-in;

    &:hover {
        transform: translateX(-3px);
        
        .minus_svg{
            fill: $blue;
    }
}
    }

.minus_svg{
    fill: #666666;
}

.kosz-container {
    display: flex;
    margin-left: 15px;
    margin-right: 15px;
    
}
.kosz {
    transition:  border 0.1s ease-in;
    width: 55px;
    height: 55px;
    text-align: center;

    border: 7px solid #666666;
    border-radius: 50%;
    background: transparent;

    color: #666666;
    font-size: 22px;
    font-weight: 700;
    line-height: 1.8;
    // & + & {
    //     margin-left: 10px;
    // }
    a{
        color: inherit;
        text-decoration: none;
        display: block;
    }
    &:hover{
        cursor: pointer;
        color: $blue;
        text-decoration: none;
        border: 9px solid $blue;
    }

}


.kosz__plus{
    display: flex;
    
}
.btn_plus{
    border: none;
    background: transparent;
    outline: none;
    padding: 0;
    cursor: pointer;
    text-align: center;
   
    transition:  transform 0.3s ease-in;

    &:hover {
        transform: translateX(3px);
        
        .plus_svg{
            fill: $blue;
    }
    }
}
.plus_svg{
    fill: #666666;
}

.dodaj{
    padding: 7px;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: 25px;
    outline: none;
    justify-content: center;
    background-color: #666666;
    border-radius: 20px;
    border: none;
    color: #ffffff;
    font-size: 25px;
    font-weight: 600;
    line-height: 1;
    transition:  background-color 0.1s ease-in;

    &:hover {
        background-color: $blue;

    }
   
}
