.copyright {
    display: flex;
}
.copyright__item {
    color: #b8b8b8;
    font-size: 14px;
    font-weight: 500;
    line-height: 2.14;

    &+&{
        margin-left: 30px;
    }
    a{
        color: inherit;
        text-decoration: none;

        &:hover{
            color: $blue;
            text-decoration: none;
        }
    }
}
