.header-link{
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #808080;
    
    .icon-svg{
        fill: gray;
    }
   
    &:hover{
        color: $blue;
        text-decoration: none;

        .icon-svg{
            fill: $blue;
        }
    }

}

.header-link__icon{
    position: relative;
    width: 21px;
    height: 21px;
    margin-right: 15px;

}

.header-link--notify .header-link__icon::after{
    position: absolute;
    top: -8px;
    right: -7px;
    content: "";
    width: 6px;
    height: 6px;
    background-color: $blue;
    border-radius: 50%;


}
.header-link__text{
   
    font-size: 16px;
    font-weight: 500;
    line-height: 2.14;

}