.sections-kontakt {
    padding-left: 15px;
    padding-right: 15px;
    display: flex;
    align-items: center;
   // justify-content: center;
    margin-bottom: 50px;
  }
  
  .kontakt-text {
    color: #808080;
    font-size: 20px;
    font-weight: 600;
    p{
        margin-top: 20px;
     }
     a{
         text-decoration: none;
         color: $blue;
         &:hover{
             color: #666666;
         }
     }
  }

  .sections-kontakt_center {
    display: flex;
    margin-top: 60px;
    margin-bottom: 30px;
   
    @extend %mixin-card;
    text-align: center;
    justify-content: center;
    padding: 0 40px;
    width: 100%;
    height: 60px;
  }

.kontakt-text_center{

    color: #666666;
    font-size: 20px;
    font-weight: 700;
    line-height: 1;
    p{
      margin-top: 20px;
   }
   a{
       text-decoration: none;
       color: $blue;
       &:hover{
           color: #666666;
       }
   }
    
}

.maps{
    @extend %mixin-card;
    margin-bottom: 70px;
}