//общий стиль
.header {
    background-color: white;
    height: 180px;
    box-shadow: 0 3px 10px rgba(0,0,0,0.08);
    margin-bottom: 30px;
}
//стиль контейнера для лога, поиска, списка и кнопки 
.header__container{
    max-width: 1050px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 25px;
    padding-right: 25px;
    padding-top: 30px;
    padding-bottom: 24px;
}
