.header__logo{
    text-decoration: none;
}
//стиль макета поиска 
.header__search{
    display: flex;
    width: 470px;
    height: 50px;
    border-radius: 4px;
    border: 2px solid #f5f5f5;

}
//поиск меняем стиль
.header__search-input-wrapper{

    border-right: 2px solid #f5f5f5;
    width: 278px;
    display: flex;
    align-items: center;
    
}

.header__search-input{
    width: 100%;
    height: 46px;
    padding-left: 20px;
    padding-right: 20px;
    border: none;
    color: rgb(116, 89, 23);
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    font-weight: 500;
    line-height: 2.14;

    &::placeholder{
        color: #b3b3b3;
    }
}
//выпадающий список меняем стиль
.header__search-dropdown{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 142px;
    border-right: 2px solid #f5f5f5;


}
//кнопка поиск меняем стиль
.header__search-button-wrapper{
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;

}
.header__search-button{
    background-color: transparent;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    cursor: pointer;

}

.header__links{
    display: flex;

}

.header__nav{
    border-top: 2px solid #f5f5f5;

}